@import "./override_card.scss";

.login1 {
  .login-wrapper-background {
    /* background-image: url("../../../../assets/images/backgrounds/soldierDarkMode.png"); */
    background-repeat: no-repeat;
    background-size: 50% 100%;
    background-position: 0 0;
   // margin-top: 70px;
  }
  .login-wrapper {
    height: 100vh; /* ارتفاع الصفحة بالكامل */
    width: 100vw; /* عرض الصفحة بالكامل */
    padding: 10% 20% 10% 10%;
    display: flex;
    align-items: flex-end; /* محاذاة المحتوى إلى الأسفل */
    justify-content: center; /* محاذاة المحتوى أفقيًا في المنتصف */
    flex-direction: column; /* ترتيب العناصر بشكل عمودي */
  }
  /* .login-wrapper {
    height: 100vh;
    width: 100vw;
    padding: 2%;
    display: flex;
    align-items: center;
    justify-self: center;
    justify-content: space-evenly;
    align-content: center;
    flex-direction: column; 
  } */

  .login-body {
    /* margin: auto !important; */
    /* position: absolute; */
    /* top: 30%;
  left: 40%; */
    width: 25em !important;
    /* height: 30em !important; */
    backdrop-filter: blur(1px);
    /* padding: 10px; */
  }

  .login-header {
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;
    align-items: center;
    height: 30%;
    width: 100%;
  }

  /* .login-body img:hover {
    animation: rotate 3s linear infinite;
      animation: spin 2s linear infinite;
    /* margin: 20px !important; */
    /* padding-top: 20px; */
    /* transform: scale(0.9); 
  } */

  .login-header-text {
    font-size: 4rem;
    font-weight: bolder;
    color: #383737;
    padding: "15px";
    text-align: center;
    margin: auto;
  }

  .login-card {
    // opacity: 1;
    margin: auto;
    /* padding-right: 5%;
    padding-left: 5%; */
    border-radius: 2%;
    /* border-radius: 15% 15% 15% 0%; */
    height: 100%;
    width: 90%;
  }

  img {
    width: 14rem !important;
    margin: 0 auto;
    margin-top: 1%;
  }
  img {
    max-width: 100%; /* يضمن عدم تجاوز حجم الصورة لعرض الحاوية */
    max-height: 100%; /* يضمن عدم تجاوز الصورة للارتفاع المتاح */
    object-fit: contain; /* يحافظ على أبعاد الصورة مع ملاءمة الحاوية */
    display: block; /* يضمن ظهور الصورة دائمًا */
  }
  img {
    width: 14rem !important;
    margin: 0 auto;
    margin-top: 1%;
  }
  



  .verification-input-container {
    justify-content: center; /* توسيط الحقول */
    gap: 16px; /* تحسين التباعد بين الحقول */
    margin-bottom: 20px;
  }
  
  /* تخصيص المدخلات */
  .verification-input-character {
    width: 60px; /* زيادة العرض */
    height: 70px; /* زيادة الارتفاع */
    text-align: center;
    font-size: 2em; /* تكبير الخط */
    font-weight: bold;
    border: none; /* إزالة الحدود */
    border-radius: 12px; /* تدوير الزوايا */
    transition: all 0.3s ease-in-out;
    outline: none;
    background-color: #f5f5f5; /* لون خلفية هادئ */
    color: #000;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* إضافة ظل */
  }
  
  /* تخصيص المدخلات عند التركيز */
  .verification-input-character:focus {
    background-color: #fff;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  }
  
  /* تخصيص المدخلات المملوءة */
  .verification-input-character-filled {
    background-color: #ffffff;
    color: #000;
  }
  
  @media (max-height: 630px) {
    img {
      width: 9rem !important; /* تقليل الحجم بدلاً من الإخفاء */
      margin: auto !important;
    }
  }
  
  @media (max-height: 500px) {
    img {
      width: 7rem !important; /* تقليل الحجم أكثر */
      margin: auto !important;
    }
  }
}